.video {
  margin-top: -113px;
  margin-left: -63px;
  position: relative;
  width: 55.15%;
  height: auto;

  &:hover,
  &:focus {
    .video__button {
      opacity: 0.7;
    }
  }

  &:active {
    .video__button {
      opacity: 0.5;
    }
  }

  @include vp-1199 {
    margin: -84px auto 0 auto;
    width: calc(266 / 678 * 100%);
    max-width: 353px;
  }

  @include vp-767 {
    width: 100%;
    max-width: 532px;
    margin: 0 auto;
  }
}

.video__link {
  display: block;
  width: 100%;
  height: auto;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(20, 54, 122, 0.5);
    z-index: 1;
  }
}

.video__link-icon {
  margin-left: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;

  @include vp-1199 {
    display: block;
    margin: 0;
    padding: 0 0 1px 1px;
    width: 12px;
    height: 12px;
  }

  @include vp-767 {
    padding: 0 0 1px 3px;
  }
}

.video__link-icon-ellipse {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include vp-1199 {
    width: 73px;
    height: 73px;
  }
}

.video__media {
  display: block;
  width: 100%;
  height: 100%;
}

.video__frame {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  z-index: 2;
}

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  display: none;
  padding: 0;
  width: 72px;
  height: 72px;
  border: none;
  border-radius: 50%;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  transform: translate(-50%, -50%);
  cursor: pointer;

  @include vp-1199 {
    width: 52px;
    height: 52px;
  }
}

.video--enabled {
  cursor: pointer;
}

.video--enabled .video__button {
  display: block;
}
